<template>
    <div class="view-wrapper" :style="{height:pageMixin_browser.clientHeight} ">
        <DP_Popup
                :display.sync="tips.show"
                :show-close="false"
                :icon="tips.type"
                background-color="white"
                :title="tips.title"
        >
            <template v-if="tips.type==='error'">
                {{tips.content}}
            </template>
            <template v-else>
                <p> 提现账款将于3个工作日发放至</p>
                <p> 您的微信账号零钱中</p>
            </template>
            <DP_Button slot="button" @click.native="closePopup">我知道了</DP_Button>
        </DP_Popup>
        <div class="panel">
            <h1 class="title">可提现余额（元）</h1>
            <div class="money">
                <div class="money dp-ellipsis">
                    <DP_CountUp :decimals="2"
                                :endnum="wallet.asset"
                                class="count_up"
                                duration="1.5"
                                prefix=""
                                separator=""
                                suffix=""
                    ></DP_CountUp>
                </div>
            </div>

            <div class="info">
                <h5 class="part_title">提现信息</h5>
                <ul>
                    <li>
                        <div class="label">提现金额</div>
                        <div class="input"><input v-model="money" type="number" placeholder="请输入提现金额"></div>
                    </li>
                    <li>
                        <div class="label">收款人</div>
                        <div class="input"><input v-model="name" type="text" placeholder="请输入收款人真实姓名"></div>
                    </li>
                </ul>
            </div>


            <div class="explanation">
                <h5 class="part_title">提现说明</h5>
                <p>
                    1、微信规定，实名认证账户每日提现为2万元，
                    最小金额为2元。未实名认证用户不能提现。

                </p>
                <p>2、提现账款会在3个工作日直接转入微信零钱。</p>
            </div>

            <div class="bottom">
                <!--                <button class="btn" @click="getMoney">提现</button>-->
                <DP_Button type="primary" @click.native="getMoney">提现</DP_Button>
            </div>

        </div>

    </div>
</template>

<script>

    import pageMixin from "../../mixin/pageMixin";
    import DP_Popup from '../../components/DP_Popup/index'
    import DP_CountUp from '../../components/DP_CountUp/index'
    import {mapGetters} from "vuex";
    // import {getMoney} from "../../api/wallet";
    import DP_Button from "../../components/DP_Button"
    import {getMoney} from "@/api/wallet";
    import {sernsorFn} from "@/utils/sensors";
    import numeral from 'numeral';
    // import {wechatLogin} from "@/utils/function";

    export default {
        metaInfo() {
            return {
                title: this.$route.meta.title || '熊猫博士', // set a title
                meta: [{                 // set meta
                    name: 'keywords',
                    content: this.$route.meta.title + ",熊猫博士"
                }],
            }
        },
        name: "cash-back",
        props: {},
        components: {
            DP_Popup,
            DP_CountUp,
            DP_Button,
        },
        data() {
            return {
                tips: {
                    show: false,
                    title: "提示",
                    content: "",
                    type: "success",
                },
                name: '',
                money: '',
                phone: "",
            }
        },
        watch: {},
        mixins: [pageMixin],
        computed: {
            ...mapGetters({
                wallet: "getWallet",
            }),
        },
        methods: {
            success() {
                this.showPopup();
            },
            getMoney() {
                sernsorFn('buttonClick', {
                    tabTitle: document.title,
                    moduleTitle: 'Referral',
                    buttonType: "普通按钮",
                    buttonName: "提现"
                })
                if (this.name && this.money) {
                    if (Number(this.money) <= 2) {
                        this.showPopup('提示', "提现金额必须大于2元", 'error');
                        return;
                    }
                    getMoney(numeral(this.money).multiply(100).value(), this.name).then(res => {
                        // eslint-disable-next-line no-unused-vars
                        const {data: {data, code, message}} = res;
                        if (code === 0) {
                            this.showPopup();
                            setTimeout(() => {
                                this.dp_go('account-center');
                            }, 3500);

                        }
                    })

                } else {
                    this.showPopup('提示', "提现信息未填写完整", 'error');
                }

            },
            showPopup(title = '提现成功', content = '', type = 'success',) {
                this.tips.title = title;
                this.tips.content = content;
                this.tips.type = type;
                this.tips.show = true;
            },
            closePopup() {
                this.tips.show = false;
            }
        },
        created() {
            this.$store.dispatch('getWallet');
        },
        mounted() {
        },
    }
</script>

<style lang='scss' scoped>
    .view-wrapper {
        position: relative;
        width: 1080px;

        .part_title {
            font-size: 45px;
            margin: 45px 0;
        }

        .panel {
            width: 960px;
            height: 1396px;
            background-color: #ffffff;
            box-shadow: 0px 0px 20px 0px #d8d8d8;
            border-radius: 30px;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-top: -698px;
            margin-left: -480px;
            padding: 90px;
            box-sizing: border-box;


            .title {
                font-size: 48px;
                font-weight: normal;
                font-stretch: normal;
                line-height: 67px;
                letter-spacing: 0px;
                color: #2b2b2b;
                text-align: center;
                margin: 40px 0;
            }

            .money {
                font-size: 100px;
                color: #fc8f1a;
                text-align: center;
                font-weight: bold;

            }

            .info {
                font-size: 39px;
                color: #6e6e6e;

                ul {
                    li {
                        display: flex;
                        height: 100px;
                        line-height: 100px;

                        .label {
                            width: 200px;
                        }

                        .input {
                            flex: 1;
                            font-size: 0;

                            input::-webkit-input-placeholder {
                                color: #70b9fd;
                            }

                            input {
                                border: none;
                                width: 100%;
                                height: 100%;
                                font-size: 39px;
                                padding: 0 25px;
                                border-radius: 0;
                                /*color: #70b9fd;*/
                                color: #fc8f1a;
                                /*font-weight: bold;*/
                            }
                        }
                    }
                }
            }

            .explanation {

                p {
                    font-size: 39px;
                    color: #6e6e6e;
                    line-height: 50px;
                    margin-bottom: 30px;


                }
            }

            .bottom {
                text-align: center;

                .btn {
                    width: 546px;
                    height: 150px;
                    background-color: #fd601a;
                    border-radius: 75px;
                    text-align: center;
                    font-size: 60px;
                    line-height: 150px;
                    border: none;
                    color: white;
                }
            }


        }

    }
</style>
